@use "fonts.scss";
@use "variables.scss";
@use "mixins.scss";
@use "visually-hidden.scss";

.video {
  text-align: center;
}

.video__question {
  @include mixins.font700();
  @include mixins.start();
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  margin-top: 17px;
  margin-bottom: 30px;
}

.video__description {
  @include mixins.start();
  text-transform: uppercase;
  line-height: 26px;
  margin-bottom: 38px;
}

@media (min-width: variables.$tablet-width) {
  .video__question {
    font-size: 30px;
    margin-top: 58px;
    margin-bottom: 30px;
  }

  .video__description {
    font-size: 18px;
    margin-bottom: 66px;
  }

  .video__image {
    width: 648px;
    height: 500px;
  }
}

@media (min-width: variables.$desktop-width) {
  .video__question {
    font-size: 30px;
    margin-top: 58px;
    margin-bottom: 30px;
  }

  .video__description {
    margin-bottom: 66px;
  }

  .video__image {
    width: 800px;
    height: 500px;
  }
}
